import React from "react";
import Modal from "react-bootstrap/Modal";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup"; //    Yup  
import "react-datepicker/dist/react-datepicker.css";
import { RadioGroup } from "./RadioGroup";
import { useDispatch } from "react-redux";
import { registerUser } from "./action_user";

import simple_logo_img from "./assets/simple_logo.PNG";
import "./modal_signup.css";

const idAndPasswordRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/; //+  
const isNotBlank = (value) => {
  return value.trim() !== "";
}; //   

const validationSchema = Yup.object().shape({
  name: Yup.string().required(" ."),
  birth: Yup.date().required(" ."),
  gender: Yup.string().required(" ."),
  // validationSchema  user_id  user_pw    
  user_id: Yup.string()
    .test("isNotBlank", " .", isNotBlank)
    .matches(idAndPasswordRegex, "+ 8  .")
    .required(" ."),

  user_pw: Yup.string()
    .test("isNotBlank", " .", isNotBlank)
    .matches(idAndPasswordRegex, "+ 8  .")
    .required(" ."),

  user_pw2: Yup.string()
    .test("isNotBlank", "  .", isNotBlank)
    .oneOf([Yup.ref("user_pw"), null], "  .")
    .required("  ."),

  nickname: Yup.string().required(" ."),
});

function Modalsignup(props) {
  const dispatch = useDispatch();

  const onSubmitHandler = (values, { setSubmitting }) => {
    console.log("Submitted Form Values:", values);
    dispatch(registerUser(values)).then((response) => {
      if (response.payload.success) {
        props.history.push("/");
      } else {
        alert("Error");
      }
      setSubmitting(false);
    });
  };

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <img
          className="simple_logo"
          src={simple_logo_img}
          alt="simple_logo"
        ></img>
        <Modal.Title id="contained-modal-title-vcenter"></Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Formik
          initialValues={{
            name: "",
            birth: null,
            gender: "",
            user_id: "",
            user_pw: "",
            user_pw2: "",
            nickname: "",
          }}
          validationSchema={validationSchema}
          onSubmit={onSubmitHandler}
        >
          {({ values, isSubmitting, setFieldValue }) => (
            <Form>
              {/*    */}
              <div className="signup_box">
                <div className="signup_input_name">
                  <div className="signup_txt input_box"></div>
                  <Field
                    type="text"
                    name="name"
                    className="input_name input"
                    placeholder=""
                  />
                  <ErrorMessage
                    name="name"
                    component="div"
                    className="error_message"
                  />
                </div>
                <div className="signup_input_birth">
                  <div className="signup_txt input_box"></div>
                  <Field
                    id="signup_birth"
                    className="input_birth input"
                    type="date"
                    dateFormat="yyyy.MM.dd"
                    shouldCloseOnSelect
                    maxDate={new Date("2009-07-12")}
                    selected={values.birth}
                    onChange={(date) => setFieldValue("birth", date)}
                    placeholderText=" "
                  />
                  <ErrorMessage
                    name="birth"
                    component="div"
                    className="error_message"
                  />
                </div>
                <div className="signup_input_gender">
                  <div className="signup_txt input_box"></div>
                  <RadioGroup className="input_radio input">
                    <Field
                      id="radio_btn_1"
                      type="radio"
                      name="gender"
                      value="man"
                    />
                    <label htmlFor="radio_btn"></label>
                    <Field
                      id="radio_btn_2"
                      type="radio"
                      name="gender"
                      value="woman"
                    />
                    <label htmlFor="radio_btn"></label>
                  </RadioGroup>
                  <ErrorMessage
                    name="gender"
                    component="div"
                    className="error_message"
                  />
                </div>
              </div>
              {/*      */}
              <div className="signup_box">
                <div className="signup_input_id">
                  <div className="signup_txt input_box"></div>
                  <Field
                    type="text"
                    name="user_id"
                    className="input_id input"
                    placeholder="+ 8 "
                  />
                  <ErrorMessage
                    name="user_id"
                    component="div"
                    className="error_message"
                  />
                </div>
                <div className="signup_input_pw">
                  <div className="signup_txt input_box"></div>
                  <Field
                    type="password"
                    name="user_pw"
                    className="input_pw input"
                    placeholder="+ 8 "
                  />
                  <ErrorMessage
                    name="user_pw"
                    component="div"
                    className="error_message"
                  />
                </div>
                <div className="signup_input_pw2">
                  <div className="signup_txt input_box"> </div>
                  <Field
                    type="password"
                    name="user_pw2"
                    className="input_pw input"
                    placeholder=" "
                  />
                  <ErrorMessage
                    name="user_pw2"
                    component="div"
                    className="error_message"
                  />
                </div>
                <div className="signup_input_id">
                  <div className="signup_txt input_box"></div>
                  <Field
                    type="text"
                    name="nickname"
                    className="input_nickname input"
                    placeholder=""
                  />
                  <ErrorMessage
                    name="nickname"
                    component="div"
                    className="error_message"
                  />
                </div>
              </div>
              <div className="btn_signup">
                <button
                  id="btn_signup_ok"
                  type="submit"
                  disabled={isSubmitting}
                >
                  
                </button>
              </div>
            </Form>
          )}
        </Formik>
      </Modal.Body>
      <Modal.Footer className="footer"></Modal.Footer>
    </Modal>
  );
}

export default Modalsignup;
